import "./styles/App.scss";
import React from "react";
import { Redirect, Route, Switch, useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { toast } from "react-hot-toast";
import axios from "axios";

// IMPORT PAGES
import MainPage from "./pages/mainPage";
import MasterPage from "./pages/masterPage";
import WeaponPage from "./pages/weaponPage";

import LoginPage from "./pages/loginPage";
import RegisterPage from "./pages/registerPage";

// IMPORT COMPONENTS
import Sidebar from "./components/sidebarComponent";
import SettingsComponent from "./components/settingsComponent";

function App() {
	const dispatch = useDispatch();
	const history = useHistory();
	const appState = useSelector((state) => state.appState);
	const db_main = useSelector((state) => state.mainDatabase);
	const isAuth = appState.isAuth;
	const token = appState.userData.token;

	const getData = React.useCallback(async () => {
		try {
			await axios
				.get("/api/data/get", {
					headers: {
						Authorization: `Bearer ${token}`,
					},
				})
				.then((response) => {
					dispatch({ type: "SYNC_DATA", state: response.data });
					toast.success("Progress from the database is received.");
				})
				.catch(function (error) {
					toast.error("Failed to retrieve progress from the database.");
					if (error.response.data.message) {
						console.log(error.response.data.message);
					} else {
						console.log(error.response.data);
					}
					if (error.response.data.isExpired) {
						dispatch({ type: "LOG_OUT" });
						dispatch({ type: "RESET_ALL" });
						history.push("/login");
					}
				});
		} catch (error) {
			console.log(error);
		}
	}, [token, dispatch, history]);

	React.useEffect(() => {
		if (isAuth) {
			getData();
		}
	}, [getData, isAuth]);

	const syncData = React.useCallback(async () => {
		try {
			await axios
				.post("/api/data/sync", JSON.stringify(db_main), {
					headers: {
						"Content-Type": "application/json",
						Authorization: `Bearer ${appState.userData.token}`,
					},
				})
				.then((response) => {
					toast.success(response.data);
				})
				.catch(function (error) {
					if (!error.response.data.message) {
						toast.error("Unable synchronize progress with database... ");
						console.log(error.response.data);
					}
					toast.error(error.response.data.message);
					console.log(error.response.data.message);
				});
		} catch (error) {
			toast.error("Something went wrong... ");
			console.log(error);
		}
	}, [appState.userData.token, db_main]);

	React.useEffect(() => {
		if (isAuth) {
			syncData();
		}
	}, [db_main, isAuth, syncData]);

	if (isAuth) {
		return (
			<div className="tracker">
				<Sidebar />
				<SettingsComponent />
				<Switch>
					<Route exact path="/">
						<MainPage />
					</Route>
					<Route path="/dm">
						<MasterPage />
					</Route>
					<Route path="/da">
						<MasterPage />
					</Route>
					<Route path="/weapon">
						<WeaponPage />
					</Route>
					<Route path="/login">
						<LoginPage />
					</Route>
					<Route path="/register">
						<RegisterPage />
					</Route>
					<Redirect to="/" />
				</Switch>
			</div>
		);
	}

	return (
		<div className="tracker">
			<Switch>
				<Route exact path="/">
					<RegisterPage />
				</Route>
				<Route path="/login">
					<LoginPage />
				</Route>
				<Route path="/register">
					<RegisterPage />
				</Route>
				<Redirect to="/" />
			</Switch>
		</div>
	);
}

export default App;
